import React, { useState, useEffect } from "react";

const MesWallets = () => {
    const [wallets, setWallets] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [debugData, setDebugData] = useState(null); //  Stocker les donnes API pour affichage

    const user = JSON.parse(localStorage.getItem("user"));
    const memberId = user ? user._id : null;

    useEffect(() => {
        if (!memberId) {
            setError(" Impossible de rcuprer l'ID du membre.");
            setLoading(false);
            return;
        }

        const fetchWallets = async () => {
            try {
                const token = localStorage.getItem("token"); 
                const url = `https://mlm-app-jhc.fly.dev/api/wallets/member/${memberId}`;

                const response = await fetch(url, {
                    headers: { "Authorization": `Bearer ${token}` }
                });

                const data = await response.json();
                setDebugData(data); //  Afficher les donnes API reues

                if (!response.ok) {
                    throw new Error(data.error || " Erreur lors de la rcupration des wallets.");
                }

                if (!Array.isArray(data)) {
                    throw new Error(" L'API ne renvoie pas un tableau !");
                }

                setWallets(data);
            } catch (err) {
                setError(err.message);
            } finally {
                setLoading(false);
            }
        };

        fetchWallets();
    }, [memberId]);

    return (
        <div>
            <h1> Mes Wallets</h1>

            <p> ID du membre : {memberId ? memberId : " Aucun ID trouv"}</p>

            {loading && <p> Chargement...</p>}
            {error && <p style={{ color: "red" }}> {error}</p>}

            <h2> Liste des Wallets</h2>

            <p> Nombre de wallets rcuprs : {wallets.length}</p>

            {wallets.length > 0 ? (
                <table border="1" cellPadding="8" cellSpacing="0">
                    <thead>
                        <tr>
                            <th> Nom du Wallet</th>
                            <th> Adresse Publique</th>
                            <th> ID Propritaire</th>
                        </tr>
                    </thead>
                    <tbody>
                        {wallets.map(wallet => (
                            <tr key={wallet._id}>
                                <td>{wallet.walletName}</td>
                                <td>{wallet.publicAddress}</td>
                                <td>{wallet.ownerId}</td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            ) : (
                <p> Aucun wallet trouv pour cet utilisateur.</p>
            )}

            {/*  DEBUG : Affichage brut des donnes API */}
            <h2> Donnes API reues :</h2>
            <pre style={{ whiteSpace: "pre-wrap", wordWrap: "break-word" }}>
                {JSON.stringify(debugData, null, 2)}
            </pre>
        </div>
    );
};

export default MesWallets;